.portfolio-page {
  min-height: 100%;
  width: 100%;
  z-index: 10;
  -webkit-flex-sizing: border-box;
          -webkit-box-sizing: border-box;
                  box-sizing: border-box;
  padding-left: 250px;
}

.portfolio-page-aligner {
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-align: center;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: distribute;
  justify-content: space-around;
  width: 100%;
  height: 100%;
}

.portfolio-page-aligner h1 {
    text-transform: uppercase;
    font-weight: 900;
    font-size: 50px;
}

.portfolio-page-aligner img {
    margin: 0 auto;
}

.gradient {
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
    background-image: rgba(33,107,246,1);
    background-image: -webkit-gradient(left top, right top, color-stop(0%, rgba(33,107,246,1)), color-stop(100%, rgba(27,86,203,1)));
    background-image: -webkit-linear-gradient(left, rgba(33,107,246,1) 0%, rgba(27,86,203,1) 100%);
    background-image: -o-linear-gradient(left, rgba(33,107,246,1) 0%, rgba(27,86,203,1) 100%);
    background-image: -webkit-gradient(linear, left top, right top, from(rgba(33,107,246,1)), to(rgba(27,86,203,1)));
    background-image: linear-gradient(to right, rgba(33,107,246,1) 0%, rgba(27,86,203,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#216bf6', endColorstr='#1b56cb', GradientType=1 );
}

.portfolio-page__content {
  text-align: center;
  padding-top: 75px;
}

.portfolio-image-aligner img {
  margin: 20px;
  vertical-align: bottom;
  border-radius: 20px;

}

.screen-text-vertical {
  font-family: 'Roboto light';
  font-size: 23px;
  -webkit-transform: rotate(-90deg);
      -ms-transform: rotate(-90deg);
          transform: rotate(-90deg);
  position: absolute;
  margin-top: 320px;
  margin-left: -40px;
  letter-spacing: 2px;
  width: 1px;
  white-space: nowrap;
}

.portfolio-section {
  padding-bottom: 70px;
}

.portfolio-section h1 {
  text-align: center;
}

.portfolio-subsection {
  padding-bottom: 40px;
}

.portfolio-palette {
  text-align: center;
  margin: 0 auto;
}
.palette-item {
  display: inline-block;
  padding: 20px;
}
.palette-item div {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  margin-bottom: 10px;
}
.palette-item p {
  font-weight: 700;
  text-transform: uppercase;
  color: #000;
}

.portfolio-page__content_title-group {
  padding-bottom: 30px;
  padding-top: 20px;
}
.portfolio-page__content_title-group hr {
  border-width: 5px;
  border-color: #000;
  width: 100px;
}
.portfolio-subtext {
  margin: 10px auto;
  text-align: center;
  font-family: 'Roboto light';
  font-size: 18px;
  max-width: 500px;
}
.uppercase {
  text-transform: uppercase;
}
.smallcaps {
  font-variant: small-caps;
}
.portfolio-typography {
  margin: 0 auto;
  max-width: 550px;
  font-size: 17px;
}
.proxima-bold {
  font-weight: 800;
}
.proxima-regular {
  font-weight: 400;
}
.proxima-light {
  font-weight: 200;
}

.portfolio-inner-aligner {
  padding-bottom: 30px;
}

.portfolio-inner-aligner img,
.portfolio-image-aligner img {
  -webkit-box-shadow: 0px 0px 32px 16px rgba(235,235,235,0.6);
  box-shadow: 0px 0px 32px 16px rgba(235,235,235,0.6);
}

@media screen and (max-width: 1400px) {
  .portfolio-page-aligner img {
    max-width: 650px;
  }

  .portfolio-image-aligner img {
    display: inline-block!important;
  }
  .portfolio-page-aligner h1 {
    font-size: 40px;
  }
  .portfolio-page-aligner h2 {
    font-size: 25px;
  }
  .portfolio-section h1 {
    font-size: 25px;
  }

  .screen-text-vertical {
    font-family: 'Roboto light';
    font-size: 14px;
    margin-top: 0;
    margin-left: 0;
    letter-spacing: 2px;
    white-space: nowrap;
    margin-bottom: 10px;
    width: unset; 
    margin: 10px auto;
    -webkit-transform: rotate(0deg);
        -ms-transform: rotate(0deg);
            transform: rotate(0deg);
    text-align: center;
    position: relative;
  }
}

@media screen and (max-width: 700px) {
  .portfolio-page-aligner img {
    max-width: 500px;
  }
  .portfolio-subtext,
  .portfolio-typography {
    max-width: 400px;
  }

}

@media screen and (max-width: 500px) {
  .portfolio-page-aligner img {
    max-width: 300px;
  }

  .portfolio-subtext,
  .portfolio-typography {
    max-width: 300px;
    font-size: 14px;
    text-align: center
  }

  .smallcaps.eczar {
    position: relative!important;
    margin-top: 0!important;
  }

  .portfolio-page-aligner h1 {
    font-size: 25px;
  }
  .portfolio-page-aligner h2,
  .portfolio-page__content_title-group h1 {
    font-size: 20px;
  }
  .palette-item,
  .palette-item>div {
    display: block;
    margin: 0 auto;
    text-align: center;
  }

  .palette-item>div {
    margin-bottom: 15px;
  }
}



