.close-popup-wrap {
  position: fixed;
  top: 50px;
  right: 50px;
  cursor: pointer;
}

@media screen and (max-width: 1025px) {
  .close-popup-wrap {
    top: 140px;
  }
}

@media screen and (max-width: 500px) {
  .close-popup-wrap {
    top: 120px;
    right: 30px;
    text-align: center;
  }
}



