.whitetext {
  color: #fff;
}
.sugarpepper-background-main {
  background-size: 120% 100%;
  background-repeat: no-repeat;
  background-image: url(../../../assets/sugarpepper-main.png);
}
.eczar {
  font-family: eczar;
}
.worskans-reg {
  font-family: worksans-reg;
}
.worksans-semibold {
  font-family: worksans-semibold;
}
.worksans-light {
  font-fmaily: worksans-light;
}
.smallcaps.eczar {
  font-size: 20px;
  line-height: 1;
  margin-top: -1px;
  position: absolute;
}

.img-shaded {
  -webkit-box-shadow: 0px 0px 32px 16px rgba(235,235,235,0.6);
  box-shadow: 0px 0px 32px 16px rgba(235,235,235,0.6);
}

.portfolio-section.offset-top {
  margin-top: 50px;
}

.portfolio-button-container {
  padding-top: 50px;
  padding-bottom: 50px;
}

.portfolio-button {
  margin: 0 auto;
  display: block;
  background: #222222;
  color: #fff;
  font-size: 18;
  padding: 15px 30px;
  line-height: 1;
  -webkit-transition: all .2s ease-out;
  -o-transition: all .2s ease-out;
  transition: all .2s ease-out;
  border: 2px solid #222222;
  cursor: pointer;
  font-weight: 600;
  letter-spacing: 2px;
}
.portfolio-button:hover {
  background: #fff;
  color: #222222;
}

@media screen and (max-width: 1025px) {
  .portfolio-page {
    padding-left: 0;
    padding-top: 50px;
  }
}



