html {
  min-height: 650px;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  min-height: 650px;
  background: #f5f5f5;
  overflow: hidden!important;
}

.homepage-react {
  -webkit-transition: all .6s ease-in-out;
  -o-transition: all .6s ease-in-out;
  transition: all .6s ease-in-out;
}

.to-reveal {
  opacity: 0;
}

.active-tab {
  z-index: 200;
}
.next-tab {
  z-index: 150;
}
a {
  color: #2b2b2b;
  outline: none;
  text-decoration: none!important;
}
a:hover {
  color: #2b2b2b;
}

.homepage__content {
  min-height: 100%;
  width: 100%;
  z-index: 10;
  -webkit-flex-sizing: border-box;
          -webkit-box-sizing: border-box;
                  box-sizing: border-box;
  padding-left: 250px;
}

.homepage__content_tab {

  width: 100%;
  height: 100%;
  -webkit-flex-sizing: border-box;
          -webkit-box-sizing: border-box;
                  box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-align: center;
  -ms-flex-align: center;
          align-items: center;
  box-sizing: border-box;
}

.centered {
  justify-content: space-around;
      -ms-flex-pack: distribute;
}

/*________________________ homepage main tab ____________________*/
.home-intro__container {
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-align: center;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: distribute;
  justify-content: space-around;
  width: 100%;
  height: 100%;
}
.home-intro__container_title {
  color: #2b2b2b;
  text-align: center;
}
.home-intro__container_title h1 {
  text-transform: uppercase;
  font-size: 70px;
  font-weight: 900;
  margin-bottom: 15px;
}
.home-intro__container_title h3 {
  font-size: 35px;
  font-weight: 600;
  letter-spacing: 1px;
  margin: 0;
}
.scroll-down-btn {
  text-align: center;
  position: absolute;
  top: 12.5%;
  cursor: pointer;
}
.scroll-down-btn i {
  font-size: 33px;
  color: #9855d4;
}

/*_____________________ homepage section 2 ( aka WHAT WE DO ) ________________________*/

.homepage__content_tab_block {
    padding-left: 15px;
    padding-right: 15px;
    /*overflow-x: hidden;*/
    margin: 0 auto;
    width: 100%;
}

.homepage__section-title {
  margin-left: 10%;
  margin-top: 45px;
}
.homepage__section-title h2 {
  font-size: 36px;
  font-weight: 600;
  margin: 0;
}
.homepage__section-title hr {
  width: 75px;
  border: 2px solid #2b2b2b;
  float: left;
}

.what-we-do__tabs {
  margin-top: 35px;
  width: 100%;
}
.what-we-do__tabs_tab {
  padding: 10px 0;
  min-height: 250px;
}
.what-we-do__tabs_tab .tab-content {
  background: #fff;
  width: 100%;
  padding: 45px;
  height: 100%;
  border-radius: 10px;
}
.tab-wrap-left {
  padding-right: 7px!important;
}
.tab-wrap-center {
  padding-right: 7px!important;
  padding-left: 8px!important;
}
.tab-wrap-right {
  padding-left: 7px!important;
}
.what-we-do-text {
  color: #2b2b2b;
  font-size: 23px;
  font-weight: 900;
  text-transform: uppercase;
  line-height: 1.2;
  margin-top: 20px;
}

.tab-content>div {
    position: absolute;
    bottom: 45px;
}

.Section {
  overflow: hidden!important;
  /*display: -webkit-flex;
  display: -ms-flexbox;*/
  /*display: flex;
  -webkit-flex-align: center!important;
  -ms-flex-align: center!important;
          align-items: center!important;
  -ms-flex-pack: distribute!important;
      justify-content: space-around!important; */
}
.slick-list {
    padding-left: 2px!important;
}
a {
  color: #000!important;
}

/* ____________________________________________media queries ______________________________________________ */

@media screen and (max-width: 1400px) {
  .home-intro__container_title h1 {
    font-size: 62px;
  }
    .homepage__section-title h2 {
         font-size: 30px;
   }

}

@media screen and (max-width: 1200px) {
  .home-intro__container_title h1 {
    font-size: 45px;
  }

   .home-intro__container_title h3 {
     font-size: 30px;
   }


}

@media screen and (max-width: 1000px), screen and (max-height: 700px) {
  .what-we-do-text {
    font-size: 18px;
  }
}


@media screen and (max-width: 768px) {
  .tab-wrap-center {
      padding-left: 15px!important;
      padding-right: 15px!important;
  }

  .tab-wrap-left {
      padding-right: 15px!important;
  }

  .tab-wrap-right {
      padding-left: 15px!important;
  }
}



